body, html {
    width: 100%;
    height: 100%;
    padding: 0px;
    margin: 0px auto;
    font-style: normal;
    font-weight: normal;
    line-height: normal;
    font-family: Roboto;
}
.menu-block {
    width: 20%;
}

#container {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2vw;
    background: #BEBEBE;
    margin: 0px;
}

